///* global $ */
import React, { Suspense, lazy } from 'react';
import { Tabs, TabsPanel, Spinner } from '@salesforce/design-system-react';

import {baseUrl, GetParams} from '../lib/grax-data-lake';

import $ from 'jquery';

const GRAXRestore = lazy(() => import('./GRAXRestore'));
const GRAXRestoreMapping = lazy(() => import('./GRAXRestoreMapping'));

export default class GRAXRestoreTabs extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            isLoading: true,
            error: null,
            customMapping: null
        };
    }

    componentDidMount() {
        $.ajax({
            type: 'GET',
            //dataType: 'jsonp',
            url: baseUrl + '/grax-app/grax-data-lake/getCustomMapping' + GetParams({action:'load'}),
            data: {},
            success: function(data) {
                var customMapping = data;

                try {
                    customMapping = $.parseJSON(data);
                } catch(e){}

                this.setState({
                    customMapping: customMapping,
                    isLoading: false
                });
            }.bind(this),
            error: function(xhr, status, err) {
                console.error(xhr, status, err);

                this.setState({
                    error: xhr.status === 404 ? null : err.toString(),
                    customMapping: xhr.status === 404 ? {} : null,
                    isLoading: false
                });
            }.bind(this)
        });
    }

    render() {
        if(this.state.isLoading) {
            return (
                <Spinner size="large" variant="brand" />
            );
        }

        if(this.props.appName === 'mapping') {
            return (
                <Suspense fallback={<Spinner size="large" variant="brand" />}>
                    <GRAXRestoreMapping customMapping={this.state.customMapping} onMappingChange={(customMapping) => {
                        this.setState({
                            customMapping: customMapping
                        });
                    }} />
                </Suspense>
            );
        }

        return (
            <Tabs variant={this.props.variant} id="grax-restore-tabs">
                {this.props.appName === 'restore' ? (
                    <TabsPanel label="Restore">
                        <Suspense fallback={<Spinner size="large" variant="brand" />}>
                            <GRAXRestore customMapping={this.state.customMapping} />
                        </Suspense>
                    </TabsPanel>
                ) : null}
            </Tabs>
        );
    }
}